import { AngularFirestore } from "@angular/fire/firestore";
import { Component, OnInit, TemplateRef } from "@angular/core";
import { Courses } from "src/app/courses/courses.model";
import { CoursesService } from "src/app/courses/courses.service";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
@Component({
  selector: "app-courses-list",
  templateUrl: "./courses-list.component.html",
  styleUrls: ["./courses-list.component.css"]
})
export class CoursesListComponent implements OnInit {
  courses: Courses[];
  filtered = "";
  modalRef: BsModalRef;
  constructor(
    private coursesservice: CoursesService,
    private modalService: BsModalService,
    private firestore: AngularFirestore
  ) {}
  ngOnInit() {
    this.coursesservice.getAll().subscribe(courses => {
      this.courses = courses
        .map(course => {
          let data = course.payload.doc.data() as Courses;
          data.id = course.payload.doc.id;
          return { ...(data as Courses) };
        })
        .slice(courses.length - 5, courses.length);
    });
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: "modal-sm" });
  }

  decline(): void {
    this.modalRef.hide();
  }

  onDeleteCourse(id: string) {
    this.firestore.doc("courses/" + id).delete();
    this.modalRef.hide();
  }
  onEdit(course) {
    this.coursesservice.course = Object.assign({}, course);
  }
}
