import { Student } from './../../students/student.model';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { StudentsService } from 'src/app/students/students.service';
import { DataStorageService } from 'src/app/shared/data-storage.service';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-students-list',
  templateUrl: './students-list.component.html',
  styleUrls: ['./students-list.component.css']
})
export class StudentsListComponent implements OnInit {

  students:Student[];
  filtered='';
  modalRef: BsModalRef;
  constructor(private studentservice:StudentsService,
    private modalService: BsModalService,
    private firestore: AngularFirestore
   ) { }

  ngOnInit() {
    this.studentservice.getAll().subscribe(students=>{
      this.students=students.map(student=>{
        let data = student.payload.doc.data() as Student;
        data.id = student.payload.doc.id;
        return { ...(data as Student)};
      }).slice(students.length-5,students.length)
    })
  }
  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: "modal-sm" });
  }

  decline(): void {
    this.modalRef.hide();
  }
  onDelete(id:string){
    this.firestore.doc("students/" + id).delete();
    this.modalRef.hide();
  }

  onEdit(student){
    console.log(student);

    this.studentservice.student = Object.assign({},student);
  }
}
