import { Subscription } from "rxjs";
import { CoursesService } from "./../courses.service";
import { Courses } from "./../courses.model";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Params, Router } from "@angular/router";

import { Student } from "./../../students/student.model";
import { StudentsService } from "src/app/students/students.service";
import { FormGroup, FormControl, FormArray, Validators } from "@angular/forms";
import { AngularFirestore } from "@angular/fire/firestore";

@Component({
  selector: "app-course-edit",
  templateUrl: "./course-edit.component.html",
  styleUrls: ["./course-edit.component.css"]
})
export class CourseEditComponent implements OnInit {
  editedCourse: Courses;
  editCourseId: number;
  editMode = false;
  students: Student[] = [];
  courseForm: FormGroup;
  subscription: Subscription;
  constructor(
    private courseservice: CoursesService,
    private studentservice: StudentsService,
    private route: ActivatedRoute,
    private router: Router,
    private firestore: AngularFirestore
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      this.editCourseId = +params["id"];
      this.editedCourse = this.courseservice.course;
      this.editMode = params["id"] != null;
      this.initForm();
      if (this.editMode) {
          this.patchEditCourseData();
        }
      });
     this.studentservice.getAll().subscribe(students=>{
      this.students=students.map(student=>{
        let data = student.payload.doc.data() as Student;
        data.id = student.payload.doc.id;
        return { ...(data as Student)};
      })
    })
  }

  patchEditCourseData() {
       this.courseForm.patchValue(this.editedCourse);
  }
  get courseStudents() {
    return (<FormArray>this.courseForm.get("students")).controls;
  }
  onSubmit() {
    let data=Object.assign({},this.courseForm.value)
    delete data.id;
    if (this.editMode) {
      this.firestore.doc("courses/"+this.editedCourse.id).update(data);
      this.studentservice.courseStudentList(data["students"],data,this.students);
      this.router.navigate(["../../"], { relativeTo: this.route });
    } else {
      this.firestore.collection("courses").add(this.courseForm.value);
      this.studentservice.courseStudentList(data["students"],data,this.students);
      this.router.navigate(["../"], { relativeTo: this.route });
    }
  }
  private initForm() {
    this.courseForm = new FormGroup({
      id: new FormControl(null),
      name: new FormControl("", Validators.required),
      description: new FormControl("", Validators.required),
      date: new FormControl("", Validators.required),
      imagepath: new FormControl("https://cnvga.reteauaedu.ro/theme/timis/pix/defaultslider.png", Validators.required),
      students: new FormControl([])
    });
  }

  onCancel() {
    if (this.editMode) {
      this.router.navigate(["../../"], { relativeTo: this.route });
    } else {
      this.router.navigate(["../"], { relativeTo: this.route });
    }
  }
}
