import { AuthGuard } from './../auth/auth.guard';
import { Routes, RouterModule } from "@angular/router";
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { CourseEditComponent } from "./course-edit/course-edit.component";
import { AllCoursesComponent } from "./all-courses/all-courses.component";

const coursesRoutes: Routes = [
  { path:"courses",component:AllCoursesComponent,canActivate:[AuthGuard]},
  { path: "courses/course", component: CourseEditComponent ,canActivate:[AuthGuard]},
  { path: "courses/course/:id", component: CourseEditComponent,canActivate:[AuthGuard] }
];

@NgModule({
  declarations: [],
  imports: [CommonModule, RouterModule.forChild(coursesRoutes)],
  exports: [RouterModule]
})
export class CoursesRoutingModule {}
