import { Courses } from "./../../courses/courses.model";
import { Student } from "./../student.model";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormArray, FormControl, Validators } from "@angular/forms";
import { CoursesService } from "src/app/courses/courses.service";
import { StudentsService } from "../students.service";
import { ActivatedRoute, Router, Params } from "@angular/router";
import { AngularFirestore } from "@angular/fire/firestore";

@Component({
  selector: "app-student-edit",
  templateUrl: "./student-edit.component.html",
  styleUrls: ["./student-edit.component.css"]
})
export class StudentEditComponent implements OnInit {
  editedStudent: Student;
  id: number;
  editMode = false;
  courses: Courses[] = [];
  studentForm: FormGroup;
  constructor(
    private courseservice: CoursesService,
    private studentservice: StudentsService,
    private route: ActivatedRoute,
    private router: Router,
    private firestore: AngularFirestore
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      this.id = +params["id"];
      this.editedStudent= this.studentservice.student;

      this.editMode = params["id"] != null;
      this.initForm();
      if(this.editMode){
        this.patchEditStudentData();
      }
    });

    this.courseservice.getAll().subscribe(courses=>{
      this.courses=courses.map(course=>{
        let data=course.payload.doc.data() as Courses
        data.id=course.payload.doc.id;
        return {...data as Courses};
      });
    })
  }

  patchEditStudentData(){
    this.studentForm.patchValue(this.editedStudent);
  }
  // onChange(course: string, isChecked: boolean) {
  //   const coursesFormArray = <FormArray>this.studentForm.controls.courses;

  //   if (isChecked) {
  //     coursesFormArray.push(new FormControl(course));
  //   } else {
  //     let index = coursesFormArray.controls.findIndex(x => x.value == course);
  //     coursesFormArray.removeAt(index);
  //   }
  // }

  get studentCourses() {
    return (<FormArray>this.studentForm.get("courses")).controls;
  }
  onSubmit() {
    let data =Object.assign({},this.studentForm.value);
    delete data.id;
    if (this.editMode) {
      this.firestore.doc("students/"+this.editedStudent.id).update(data);
      this.courseservice.studentCourseList(data["courses"],data,this.courses);
      this.router.navigate(["../../"], { relativeTo: this.route });
    } else {
      this.firestore.collection('students').add(this.studentForm.value);
      this.courseservice.studentCourseList(data["courses"],data,this.courses);
      this.router.navigate(["../"], { relativeTo: this.route });
    }

  }
  private initForm() {
    this.studentForm = new FormGroup({
      name: new FormControl("", Validators.required),
      phone: new FormControl("", Validators.required),
      imagepath: new FormControl("https://d2ln1xbi067hum.cloudfront.net/assets/default_user-951af10295a22e5f7fa2fa6165613c14.png", Validators.required),
      id: new FormControl(null),
      courses: new FormControl([])
    });
  }

  onCancel() {
    if (this.editMode) {
      this.router.navigate(["../../"], { relativeTo: this.route });
    } else {
      this.router.navigate(["../"], { relativeTo: this.route });
    }
  }
}
