import { AuthService } from "./../auth/auth.service";
import { Http, Response } from "@angular/http";
import { Courses } from "./../courses/courses.model";
import { StudentsService } from "src/app/students/students.service";
import { CoursesService } from "src/app/courses/courses.service";
import { Injectable } from "@angular/core";
import "rxjs/Rx";
import { Student } from "../students/student.model";
import { HttpClient, HttpParams, HttpRequest } from "@angular/common/http";
@Injectable({
  providedIn: "root"
})
export class DataStorageService {
  constructor(
    private httpClient: HttpClient,
    private courseservice: CoursesService,
    private studentsservice: StudentsService,
    private authservise: AuthService
  ) {}

  //Coourses
  storeCourses() {
    const token = this.authservise.getToken();
    const req = new HttpRequest('PUT','https://courses-project-128e7.firebaseio.com/courses.json',this.courseservice.getAll(),{
    reportProgress:true,params:new HttpParams().set('auth',token)});
    return this.httpClient.request(req);

  }

  getCourses() {
    const token = this.authservise.getToken();
    return this.httpClient
      .get<Courses[]>(
        "https://courses-project-128e7.firebaseio.com/courses.json?auth=" +
          token,
        {
          observe: "body",
          responseType: "json"
        }
      )
      .map(courses => {
        for (const course of courses) {
          if (!course["students"]) {
            course["students"] = [];
          }
        }
        return courses;
      });
  }

  //Students
  storeStudents() {
    const token = this.authservise.getToken();

    return this.httpClient.put(
      "https://courses-project-128e7.firebaseio.com/students.json",
      this.studentsservice.getAll(),
       {
        observe: "body",
        params: new HttpParams().set('auth',token)
      }
    );
  }

  getStudent() {
    const token = this.authservise.getToken();
    return this.httpClient
      .get<Student[]>(
        "https://courses-project-128e7.firebaseio.com/students.json",
        {
          observe: "body",
          params: new HttpParams().set('auth',token)
        }
      )
      .map(students => {
        for (const student of students) {
          if (!student["courses"]) {
            student["courses"] = [];
          }
        }
        return students;
      });
  }
}
