import { Routes, RouterModule } from "@angular/router";
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { StudentEditComponent } from "./student-edit/student-edit.component";
import { AllStudentsComponent } from "./all-students/all-students.component";
import { AuthGuard } from "../auth/auth.guard";

const studentsRoutes: Routes = [
  { path: "students", component: AllStudentsComponent,canActivate:[AuthGuard]},
  { path: "students/student", component: StudentEditComponent ,canActivate:[AuthGuard]},
  { path: "students/student/:id", component: StudentEditComponent ,canActivate:[AuthGuard]}
];

@NgModule({
  declarations: [],
  imports: [CommonModule, RouterModule.forChild(studentsRoutes)],
  exports: [RouterModule]
})
export class StudentsRoutingModule {}
