import { AngularFirestore } from "@angular/fire/firestore";
import { CoursesService } from "./../courses/courses.service";
import { Courses } from "./../courses/courses.model";
import { Injectable, OnInit } from "@angular/core";
import { Student } from "./student.model";

@Injectable({
  providedIn: "root"
})
export class StudentsService implements OnInit {
  student: Student;
  studentList = [];
  check:boolean;
  constructor(
    private courseservice: CoursesService,
    private firestore: AngularFirestore
  ) {}
  ngOnInit() {
    this.getAll().subscribe(students => {
      this.studentList = students.map(student => {
        let data = student.payload.doc.data() as Student;
        data.id = student.payload.doc.id;
        return { ...(data as Student) };
      });
      return this.studentList;
    });
  }
  getAll() {
    return this.firestore.collection("students").snapshotChanges();
  }
  courseStudentList(
    CourseStudents: Student[],
    course: Courses,
    studentList
  ) {
    for (const student of studentList) {
      const stdData = Object.assign({}, student);
      for (const std of CourseStudents) {
        if (stdData.name === std) {
          this.check = stdData.courses.includes(course.name);
          console.log(this.check);
          if (!this.check) {
            stdData.courses.push(course.name);
            this.firestore
              .doc("students/" + stdData.id)
              .update({ courses: stdData.courses });
          }
        }
      }
    }
  }
}
