import { StudentsService } from 'src/app/students/students.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Student } from './../student.model';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { PageChangedEvent } from "ngx-bootstrap/pagination";



@Component({
  selector: 'app-all-students',
  templateUrl: './all-students.component.html',
  styleUrls: ['./all-students.component.css']
})
export class AllStudentsComponent implements OnInit {
 students:Student[] = [];
 filtered = "";
 returnedArray: Student[];
 modalRef: BsModalRef;
  constructor(
    private studentservice:StudentsService,
    private modalService: BsModalService,
    private firestore:AngularFirestore
    ) { }

  ngOnInit() {
    this.studentservice.getAll().subscribe(students=>{
      this.students=students.map(student=>{
        let data=student.payload.doc.data() as Student;
        data.id=student.payload.doc.id;
        return{...data as Student}
      });
      this.returnedArray = this.students.slice(0, 10);
    });
  }

  pageChanged(event: PageChangedEvent, students = this.students): void {
    const startItem = (event.page - 1) * event.itemsPerPage;
    const endItem = event.page * event.itemsPerPage;
    this.returnedArray = students.slice(startItem, endItem);
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: "modal-sm" });
  }

  decline(): void {
    this.modalRef.hide();
  }

  onEdit(student){
    this.studentservice.student = Object.assign({},student);

  }

  onDelete(id:string){
    this.firestore.doc("students/"+id).delete();
    this.modalRef.hide();
  }

}
