import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { Component, OnInit, TemplateRef } from "@angular/core";
import { Courses } from "../courses.model";
import { CoursesService } from "../courses.service";
import { AngularFirestore } from "@angular/fire/firestore";
import { PageChangedEvent } from "ngx-bootstrap/pagination";

@Component({
  selector: "app-all-courses",
  templateUrl: "./all-courses.component.html",
  styleUrls: ["./all-courses.component.css"]
})
export class AllCoursesComponent implements OnInit {
  courses: Courses[] = [];
  filtered = "";
  returnedArray: Courses[];
  modalRef: BsModalRef;
  constructor(
    private coursesservice: CoursesService,
    private modalService: BsModalService,
    private firestore: AngularFirestore
  ) {}
  ngOnInit() {
    this.coursesservice.getAll().subscribe(courses => { 
      this.courses = courses.map(course => {
        let data = course.payload.doc.data() as Courses;
        data.id = course.payload.doc.id;
        return { ...(data as Courses) };
      });
      this.returnedArray = this.courses.slice(0, 10);
    });
  }

  pageChanged(event: PageChangedEvent, courses = this.courses): void {
    const startItem = (event.page - 1) * event.itemsPerPage;
    const endItem = event.page * event.itemsPerPage;
    this.returnedArray = courses.slice(startItem, endItem);
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: "modal-sm" });
  }

  decline(): void {
    this.modalRef.hide();
  }

  onEdit(course) {
    this.coursesservice.course = Object.assign({}, course);
  }

  onDeleteCourse(id: string) {
    this.firestore.doc("courses/" + id).delete();
    this.modalRef.hide();
  }
}
